#book {
    width: 100%;
    max-width: 600px;
    display: -ms-flexbox;
    display: flex;
    margin: 2rem auto;
    -webkit-box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 30px, rgba(0, 0, 0, 0.23) 0px 6px 10px;
            box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 30px, rgba(0, 0, 0, 0.23) 0px 6px 10px;
    min-height: calc( 100vh - 4rem );
}
.aligned-vert {
    margin: auto;
    text-align: center;
    -webkit-transition: all 350ms ease-in-out;
    -o-transition: all 350ms ease-in-out;
    transition: all 350ms ease-in-out;
}
#book .align-left {
    display: -ms-flexbox;
    display: flex;
    text-align: left;
    padding-left: 2rem;
    margin-top: 3rem;
    border-bottom: 1px solid #dadada;
    padding-bottom: 3rem;
}
.wrapper-img {
    height: 100px;
    width: 100px;
    border-radius: 10em;
    overflow: hidden;
    margin: 0 auto auto 0;
    -webkit-box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 30px, rgba(0, 0, 0, 0.23) 0px 6px 10px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 30px, rgba(0, 0, 0, 0.23) 0px 6px 10px;
}
.wrapper-item {
    width: calc( 100% - 5rem - 100px);
    margin: auto 2rem auto auto;
    padding: 5px 10px;
    -webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
            box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
}
.item-number {
    font-family: monospace;
    text-transform: capitalize;
}
.item-number a {
    margin-bottom: .5rem;
    display: block;
}
.item-page, .item-author {
    font-size: 1.25rem;
    font-family: monospace;
}
.item-page {
    margin-bottom: .7rem;
}


